* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: radial-gradient(circle, #00ccff, #0a5268);
  background-attachment: fixed;
}

.todo-app {
  background: #fff;
  width: 720px;
  min-height: 780px;
  height: 100%;
  margin: 3% auto;
  color: #505050;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, .2);
}

.todo-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 600px;
  height: 100%;
}


@media screen and (max-width:425px) {
  body {
    background: #fff;
  }
  .wrapper {
    height: 100%;
  }
  .todo-app {
    width: 100%;
    border-radius: 0px;
    max-height: 100%;
    min-height: 0;
  }
  .todo-container {
    min-height: 100%;
  }
}