header {
    font-size: 2em;
    /*border-bottom: 1px solid #505050; */
    margin: 0 auto;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    background: url(../bg.jpg);
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    z-index: 1;
}

header p {
    font-size: 1em;
    position: static;
    left: 0;
    bottom: 0;
}

@media screen and (max-width: 425px) {
    header {
        font-size: 1.5em;
        border-radius: 0;
        height: 100px;
        position: fixed;
        top: 0;
        left: 0;
    }
    header p {
        font-size: 0.8em;
    }
}