.todo-list {
    list-style: none;
    z-index: 0;
  }
  
  .todo-item {
    display: flex;
    flex-direction: row;
    height: 60px;
    align-items: center;
    cursor: pointer;
    border-bottom: 1px solid #c7c7c7;
    font-size: 1.5em;
    padding: 15px;
  }

  .todo, .todo-done {
    width: 100%;
    margin-left: 10px;
    transition: all 0.1s ease;
  }

  .todo-done {
    color: #c7c7c7;
    text-decoration: line-through;
  }

  .todo input {
    border: none;
    font-size: 1em;
    cursor: pointer;
  }
  .todo input:focus {
    outline: none;
  }
  .todo-input {
    color: #505050;
  }
  .todo-input-done {
    color: #c7c7c7;
    text-decoration: line-through; 
  }

  .todo-remove {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
  }

  .fa-circle {
    color: #c7c7c7;
    transition: all 0.3s ease;
  }
  .fa-circle:hover {
    color: #58cc58;
  }
  .fa-circle-done {
    color: #58cc58;
  }

  .fa-edit {
    color: #c7c7c7;
  }
  .fa-edit:hover {
    color: #505050;
  }

  .fa-times {
    color: #c7c7c7;
    transition: all 0.1s ease;
    border-radius: 50%;
  }
  .fa-times:hover {
    color: red;
    transform: scale(1.25);
    background: #ff000025;
    padding: 3px 7px;
  }
  .fa-times-done {
    color: red;
    transform: scale(1.25);
    background: #ff000025;
    padding: 3px 7px;
  }

@media screen and (max-width: 425px) {
  .todo-list {
      position: relative;
      width: 100%;
      height: 100%;
      top: 100px;
      background: #fff;
  }
}