.form {
    display: flex;
    flex-direction: row;
    height: 60px;
    border-top: 1px solid #c7c7c7;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    background: #fff;
    z-index: 1;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}
.form .fa {
    color: #505050;
}
.form button {
    border: none;
    cursor: pointer;
    background: none;
}
.form button:focus {
    outline: none;
}

.form input {
    width: 100%;
    height: 30px;
    font-size: 1.5em;
    padding-left: 10px;
    border: none;
    outline: none;
}

.form i {
    font-size: 2em;
}

.form .clear {
    width: 100px;
}

@media screen and (max-width: 425px) {
    .form {
        position: fixed;
        width: 100%;
        bottom: 0;
        left: 0;
        background: #d8d8d8;
        color: #202020;
        padding: 0 10px;
        border-radius: 0;
    }
    .form input {
        background: #d8d8d8;
    }
    .form i {
        color: #202020;
    }
}